.PanelHeaderButton {
  appearance: none;
  background: none;
  border: 0;
  box-shadow: none;
  display: block;
  color: currentColor;
  padding: 0;
  margin: 0;
  position: relative;
}

.PanelHeaderButton[disabled] {
  opacity: 0.6;
}

.PanelHeaderButton--primitive {
  block-size: 48px;
  line-height: 48px;
  padding-block: 0;
  padding-inline: 10px;
}

/*
 * iOS
 */

.PanelHeaderButton--ios {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 17px;
}

.PanelHeaderButton--ios.PanelHeaderButton--primitive {
  block-size: 44px;
  line-height: 44px;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton--ios :global(.vkuiIcon--24) {
  padding: 10px;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton--ios :global(.vkuiIcon--28) {
  padding: 8px;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderBack--ios :global(.vkuiIcon--w-20) {
  padding-inline-start: 4px;
}

/*
 * Android
 */

.PanelHeaderButton--android.PanelHeaderButton--notPrimitive {
  border-radius: 50%;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton--android :global(.vkuiIcon--24),
.PanelHeaderButton--android :global(.vkuiIcon--28) {
  padding: 10px;
}

/**
* VKCOM
*/
.PanelHeaderButton--vkcom {
  color: var(--vkui--color_text_secondary);
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderBack--vkcom :global(.vkuiIcon) {
  color: var(--vkui--color_icon_secondary);
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton--vkcom > :not(:global(.vkuiInternalBadge)):not(:global(.vkuiInternalCounter)) {
  transition: opacity 0.3s;
  opacity: 0.7;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton--vkcom.PanelHeaderButton--hover
  > :not(:global(.vkuiInternalCounter)):not(:global(.vkuiInternalBadge)),
.PanelHeaderButton--vkcom.PanelHeaderButton--active
  > :not(:global(.vkuiInternalCounter)):not(:global(.vkuiInternalBadge)) {
  opacity: 1;
}

.PanelHeaderButton--vkcom {
  display: flex;
  align-items: center;
  padding: 10px;
}

.PanelHeaderButton--vkcom.PanelHeaderButton--notPrimitive {
  border-radius: 50%;
}

.PanelHeaderBack--vkcom.PanelHeaderBack--has-label {
  padding-inline-end: 11px;
}

@media (--sizeX-regular) {
  .PanelHeaderBack--ios.PanelHeaderBack--has-label:not(.PanelHeaderBack--sizeX-compact) {
    padding-inline-end: 8px;
  }
}

.PanelHeaderBack--sizeX-compact.PanelHeaderBack--ios .PanelHeaderButton__label {
  display: none;
}

@media (--sizeX-compact) {
  .PanelHeaderBack--sizeX-none.PanelHeaderBack--ios .PanelHeaderButton__label {
    display: none;
  }
}

/*
 * CMP:
 * Counter
 */

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton :global(.vkuiInternalCounter) {
  position: absolute;
  padding: 0;
  inset-block-start: 4px;
  inset-inline-end: 4px;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton :global(.vkuiIcon--24) ~ :global(.vkuiInternalCounter),
.PanelHeaderButton--ios :global(.vkuiInternalCounter) {
  inset-block-start: 2px;
  inset-inline-end: 2px;
}

/*
 * CMP:
 * Badge
 */

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton :global(.vkuiInternalBadge) {
  position: absolute;
  inset-block-start: 8px;
  inset-inline-end: 8px;
}

/* stylelint-disable-next-line selector-pseudo-class-disallowed-list */
.PanelHeaderButton :global(.vkuiIcon--24) ~ :global(.vkuiInternalBadge),
.PanelHeaderButton--ios :global(.vkuiInternalBadge) {
  inset-block-start: 6px;
  inset-inline-end: 6px;
}
